.footer {
  align-self: stretch;
  flex-direction: row;
  padding: var(--padding-21xl) 80px;
  box-sizing: border-box;
  gap: 80px;
  max-width: 1440px;
  border-top: 1px;
  border-top-style: solid;
  border-top-color: #333;
  margin-left: auto;
  margin-right: auto;
  text-align: left;
  color: var(--color-darkgray);
}
.react {
  position: relative;
  width: 100%;
  overflow: hidden;
  flex-direction: column;
  gap: 9px;
  text-align: center;
  font-size: var(--font-size-base);
  color: var(--m3-sys-light-on-primary);
  font-family: var(--font-roboto);
}
@media screen and (max-width: 1200px) {
  .appPageBanner {
    padding: var(--padding-21xl);
    box-sizing: border-box;
  }
  .accordionColumn {
    width: 70% !important;
    margin-left: auto;
    margin-right: auto;
  }
  .footer {
    padding: var(--padding-5xl) var(--padding-21xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 960px) {
  .headerMenu {
    display: none;
  }
  .introducingTheMyrideContainer {
    font-size: 46px;
  }
  .bannerImageIcon {
    display: none;
  }
  .bookIcon {
    width: 15% !important;
  }
  .bookCard,
  .bookCard1,
  .bookCard2 {
    width: 100% !important;
    flex: unset;
    align-self: stretch;
  }
  .bookingCardRow {
    flex-direction: column;
    gap: var(--gap-xl);
  }
  .accordionColumn {
    width: 100% !important;
  }
  .becomeDriverText {
    margin-right: 0 !important;
    margin-top: 20px !important;
    flex: unset;
    align-self: stretch;
  }
  .becomeDriverSection {
    flex-direction: column;
    padding: var(--padding-11xl);
    box-sizing: border-box;
    align-items: start !important;
    gap: var(--gap-xl);
  }
  .footerMenu {
    flex: unset;
    align-self: stretch;
  }
  .footer {
    flex-direction: column;
    padding: var(--padding-21xl);
    box-sizing: border-box;
    gap: var(--gap-xl);
    margin-top: 30px;
  }
}
@media screen and (max-width: 420px) {
  .bookRidesOn {
    font-size: var(--font-size-lg);
  }
  .introducingTheMyrideContainer {
    font-size: var(--font-size-13xl);
  }
  .bannerText {
    width: 100% !important;
    padding: 24px !important;
  }
  .appPageBanner {
    padding-left: 0;
    padding-right: 0;
    box-sizing: border-box;
  }
  .quickAndEasy {
    font-size: var(--font-size-lg);
    margin-bottom: 18px !important;
  }
  .howDoesIt {
    font-size: var(--font-size-13xl);
    line-height: 1rem;
  }
  .bookIcon {
    width: 46% !important;
  }
  .carImageIcon {
    width: 40% !important;
    height: auto !important;
  }
  .footerMenu {
    flex-direction: column;
    gap: var(--gap-21xl);
  }
  .footer {
    padding: var(--padding-11xl) var(--padding-5xl);
    box-sizing: border-box;
  }
}
