@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;0,700;1,700&display=swap");
@import url("https://fonts.googleapis.com/icon?family=Material+Icons");

body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-roboto: Roboto;

  /* font sizes */
  --font-size-base: 16px;
  --font-size-xl: 20px;
  --font-size-13xl: 32px;
  --font-size-26xl: 45px;
  --font-size-7xl: 26px;
  --font-size-lg: 18px;

  /* Colors */
  --color-gray: #242424;
  --m3-sys-light-on-primary: #fff;
  --color-darkgray: #b3b3b3;
  --color-orange: #fba403;

  /* Gaps */
  --gap-xl: 20px;
  --gap-21xl: 40px;
  --gap-xs: 12px;
  --gap-base: 16px;
  --gap-5xs: 8px;
  --gap-sm: 14px;
  --gap-11xl: 30px;

  /* Paddings */
  --padding-21xl: 40px;
  --padding-5xl: 24px;
  --padding-11xl: 30px;
  --padding-xl: 20px;
  --padding-xs: 12px;

  /* border radiuses */
  --br-29xl: 48px;
}
